import { CList } from "@components";
import { Col, Row } from "antd";
import React from "react";

function CDetailCodeActiva({ data }) {
  return (
    <Row>
      <Col span={24}>
        <CList label="Code" value={data.code} color="#D3D3D3" />
        <CList label="Name" value={data.name} color="#FFFFFF" />
      </Col>
    </Row>
  );
}

export default CDetailCodeActiva;
